body, html {
  font-size: 10px;
  height: 100%;
}

@media (min-width: 576px) {
  body, html {
    font-size: 16px;
  }
}

@media (min-width: 1200px) {
  body, html {
    font-size: 20px;
  }
}

.App {
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  font-family: 'Roboto';
}

.App hr {
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.App .nav-link {
  color: inherit!important;
}

#profilePic {
  height: 200px;
  width: 200px;
  border-radius: 100%;
}

.carousel-caption {
  background-color: black;
  opacity: .69;
  left: 15.23%;
  right: 15.23%;
}



.carousel-caption h3 {
  font-size: 3rem!important;
}

.Work i {
  font-size: 4rem;
}
.carousel a {
  color: black;
}

.carousel a:hover {
  color: grey;
  border-bottom: 0px;
}

